import { disablePageScroll, enablePageScroll, clearQueueScrollLocks, getScrollState } from 'scroll-lock';


// MENU

const navToggle = document.getElementById('nav-toggle');
const headerMenu = document.getElementById('header__menu');
const wrapper = document.querySelector('.wrapper');
const body = document.querySelector('body');
const lockPadding = document.querySelectorAll(".lock-padding");

navToggle.addEventListener('click', function(){
	navToggle.classList.toggle('active');
	headerMenu.classList.toggle('active');
	wrapper.classList.toggle('active');

	if(navToggle.matches('.active')) {
		disablePageScroll(headerMenu);
	} else {
		enablePageScroll(headerMenu);
	}
})

document.addEventListener('click', function(e) {
	if(!e.target.matches('.nav-toggle, .nav-toggle *, .header__menu, .header__menu *')) {
		// closeMenu();
		navToggle.classList.remove('active');
		headerMenu.classList.remove('active');
		wrapper.classList.remove('active');
		enablePageScroll(headerMenu);
	}
})


//Fixed Header

	var headerStyle = window.getComputedStyle(document.getElementById('header'));
	var headerAbsoluteTop = parseInt(headerStyle.getPropertyValue('top'));

    const header = document.getElementById('header');
    var headerHeight = document.getElementById('header').offsetHeight;
    var scrollOffset = window.pageYOffset;

    document.addEventListener('scroll', function(e) {
		var scrollOffset = window.pageYOffset;
        checkScroll(scrollOffset);
		// console.log(scrollOffset);
    })

    function checkScroll(scrollOffset) {
        if(scrollOffset > headerHeight + headerAbsoluteTop) {
            header.classList.add('fixed')
        } else {
            header.classList.remove('fixed')
        }
    }

// Scroll on click

const LinkForScroll = document.querySelectorAll('[data-goto]');
if (LinkForScroll.length > 0) {
    LinkForScroll.forEach(link => {
        link.addEventListener('click', clickOnLink);
    });

    function clickOnLink(e) {
        const link = e.target;
        if (link.dataset.goto && document.querySelector(link.dataset.goto)) {
            const gotoBlock = document.querySelector(link.dataset.goto);
            const gotoBlockValue = gotoBlock.getBoundingClientRect().top + pageYOffset - document.querySelector('header').offsetHeight;
            
            // if (navToggle.classList.contains('active')) {
            //     closeMenu(doUnlock = true);
            // }

            window.scrollTo({
                top: gotoBlockValue,
                behavior: 'smooth'
            });
            e.preventDefault();
        }
    }
}
